#root {
    height: 100vh;
    position: relative;
}

.encointer-map {
    position: relative;
    height: 100% !important;
    width: 100%;
    overflow: hidden !important;
    border: 0 !important;
}
.encointer-map > .component-wrapper {
    overflow: hidden !important;
    position: relative;
    height: 100%;
}

.encointer-map .details-sidebar {
    background: white;
}

.encointer-map .details-sidebar .message {
    overflow-wrap: break-word;
}

.encointer-map-wrapper {
    overflow: hidden !important;
    height: 100%;
    position: relative;
    border: 0 !important;
    margin: 0;
    padding: 0 !important;
    background: black;
}

.encointer-map-wrapper > .leaflet-container {
    height: 100%;
    width: 100%;
    z-index: 0;
}

.encointer-map .details-sidebar > .ui.segment {
    background: none;
}

.encointer-map .encointer-map-floating-widgets {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    margin-top: 0 !important;
    padding-top: 1px !important;

    width: auto !important;
    background: transparent !important;
    border: none !important;
    box-shadow: none !important;
}

.encointer-map .encointer-map-floating-widgets .encoiner-menu-button {
    width: 100% !important;
}

.encointer-map .encointer-map-node-info {
    z-index: 1;
    position: absolute !important;
    bottom: -5px;
    left: .5em;
    margin-top: 0 !important;
    padding-top: 0 !important;
}
.encointer-map .encointer-map-node-info > div {
    padding: .5em;
}

.encointer-map .encointer-map-block-number {
    z-index: 1;
    position: absolute !important;
    top: .5em;
    left: 5.5em;
    margin-top: 0 !important;
    padding-top: 0 !important;
    width: auto !important;
    background-color: #aad3df7a !important;
}

.encointer-map .encointer-map-block-number .content {
    display: flex;
    padding: 0 5px 0 5px !important;
}

.encointer-map .encointer-map-block-number div {
    margin: 0;
}

.encointer-map .encointer-map-block-number .statistic.small > .value {
    font-size: 2rem !important;
}

.encoiner-community-icon {
    text-align: center;
    background-clip: padding-box;
    border-radius: 20px;
    width: 30px !important;
    height: 30px !important;
    border: 2px solid rgba(110,204,57,0.6);
    background-color: rgba(81,204,0,1);
    box-shadow: 0px 0px 3px rgba(20,20,20,0.45);
    text-shadow: 2px -1px 3px rgba(12,60,2,1), 0px 2px 2px rgba(20,155,1,1 );
}

.encoiner-community-icon > i {
    top: -2px;
    display: block;
    position: relative;
    font-size: 18px;
    line-height: 30px;
    color: #fff;
    font-weight: bold;
    padding-bottom: 6px;
    font-style: normal;
}
